@import url("https://fonts.googleapis.com/css2?family=Ubuntu&display=swap");

/*===============================================================================
 * Global
 *===============================================================================*/

* {
  font-family: "Ubuntu", sans-serif;
}

html {
  font-size: 14px;
}

/*===============================================================================
 * Header
 *===============================================================================*/

header {
  padding-top: 20px;
  border-bottom: 1px solid lightgray;
}

/*===============================================================================
 * Main
 *===============================================================================*/

main {
  margin-top: 50px;
}

main .row > div:first-child {
  border-right: 1px solid lightgray;
}

main h3 {
  padding: 10px;
  border-radius: 10px;
  text-align: center;
  margin-bottom: 20px;
  background-color: rgb(251, 181, 52);
}

main #tbl-customers thead tr {
  background-color: lightgrey;
}

main #tbl-customers th {
  border-bottom-color: #989898;
}

main #tbl-customers td {
  vertical-align: middle;
}

main #tbl-customers th:first-child {
  width: 75px;
  text-align: center;
}

main #tbl-customers th:last-child {
  position: relative;
  width: 75px;
  text-align: center;
}

/*===============================================================================
 * Footer
 *===============================================================================*/

footer {
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 10px;
  border-top: 1px solid lightgrey;
}

/*===============================================================================
 * Table
 *===============================================================================*/
tr > td:first-child {
  text-align: center;
}

tr > td:last-child {
  text-align: center;
}

tbody > tr:hover {
  cursor: pointer;
}
.del-btn,
.del-btn:hover {
  width: 32px;
  height: 32px;
}

.del-btn {
  content: url(../img/trash.png);
}
.del-btn:hover {
  content: url(../img/trash-hover.png);
}
